@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(25, 25, 37);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Login */
.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 20px;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
  }
  .google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
  }
  .google-btn:hover {
    box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
background: #1669f2;
}
.google-icon-wrapper {
position: absolute;
margin-top: 1px;
margin-left: 1px;
width: 40px;
height: 40px;
border-radius: 2px;
background-color: #fff;
}
.google-icon {
position: absolute;
margin-top: 11px;
margin-left: -8px;
width: 18px;
height: 18px;
}
.btn-text {
float: right;
margin: 11px 11px 0 0;
color: #fff;
font-size: 14px;
letter-spacing: 0.2px;
font-family: "Roboto";
}

/* App */
.App {
  font-family: "Anton";
  font-feature-settings: auto;
  -webkit-font-kerning: auto;
          font-kerning: auto;
  text-align: center;
  padding: 2% 5%;
  color: white;
}

.App * {
    box-sizing: border-box;
}
.Selections {
  display: flex;
}
ul {
  padding: 0;
  list-style: none;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}

.showSuggestions {
  display: block;
}

.number-input{
  width: 100px;
  text-align: center;
}

.city{
  text-align: center;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}

.city {
  width: 250px;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin-top: 80px;
  margin: auto;
  position: relative;
}

.alert {
  font-family: 'Anton', sans-serif;
  font-size: 16px;
  text-align: center;
  margin: 0 auto,
}

.suggestions {
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 85px;
  font-size: 14px;
  background-color: rgba(0, 0, 0, 0.75);
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  font-family: "Anton";
  text-align: center;
  padding: 8px 6px 8px 10px;
  background-color: rgb(25, 25, 37, .75);
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  transition: .5s;
  background-color: #4285f4;
}

.event {
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

@media (min-width: 1024px) {
  .event-list {
    display: flexbox;
    flex-flow: column;
    -webkit-column-count: 2;
            column-count: 2;
  }

  .event-li {
    display: block;
  }
}
.link {
  color: lightblue;
}

.details-button{
  font-family: inherit;
  position: absolute;
  right: 5px;
  bottom: 5px;
  display: block;
  color: rgb(25, 25, 37);
  font-size: 120%;
  background: white;
  border: none;
  border-radius: 4px;
  margin: 0 15px;
  cursor: pointer;
  transition: .5s;
}

.detials-button:before{
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  height: inherit;
  width: inherit;
  transform: scale(.9);
  z-index: -1;
  transition: .5s;
}

.detials-button:hover:before{
  -webkit-filter: blur(3px);
          filter: blur(3px);
  transform: scale(1.2);
  box-shadow: 0 0 20px #4285f4;
}

.details-button:hover{
  color: #4285f4;
  box-shadow: 0 0 20px #4285f4;
  text-shadow: 0 0 20px #4285f4;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.numberOfEvents {
  width: 300px;
  margin: 20px auto;
  text-align: center;
  display: Flex;
  flex-direction: column;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

